
/*skill outer box----------------------------------------------*/
.skill-outer-box{
	padding: 4vw 0vw 4vw 3.5vw;
}

/*skills img -----------------------------------------------------*/
.skills-img-box{
/*	height: 50vw;*/
	margin: 0vw 0vw 0vw 0vw;
	padding: 6vw 0vw 6vw 4vw;
	background-image: url('../img/pSkills.png');
	background-color: #5e17eb;
	background-size: 48vw 42vw;
	background-repeat: no-repeat;
	background-position: right center;
	overflow: hidden;
}
.skills-img-box h1{
	width: 55%;
	font-family: 'Poppins',sans-serif;
	font-size: 9.5vw;
	font-weight: 600;
	color: #5e17eb;
	color: white;
	text-shadow: .2vw .2vw .2vw black;
}

/*skills item------------------------------------------------------*/
.skill-item-content-box{
	padding: 1vw 0px;
}
.skill-item-box{
	height: 15vw;
	padding: 0px;
}
.skill-item-img{
	height: 80%;
}
.skill-item-img img{
	height: 100%;
	width: 100%;
}
.skill-item-name{
	text-align: center;
	font-family: 'Poppins',sans-serif;
	font-size: 1.5vw;
	font-weight:500;
/*	color: #5e17eb;*/
	color: black;
	padding-top: .5vw;
}





/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
/*about outer-----------------------------------------------*/
	.skill-outer-box{
	padding: 0vw 0vw 4vw 0vw;

	}

	/*skills img -----------------------------------------------------*/
	.skills-img-box{
	height: 150vw;
    margin: 0vw 0vw 0vw 0vw;
	padding: 6vw 0vw 6vw 4vw;
    background-size: 100% 88vw;
    background-position: right bottom;
	}
	.skills-img-box h1{
	width: 100%;
    font-size: 16vw;
    padding: 0vw 14.2vw 0vw 0vw;
	}

	/*personal-----------------------------------------------------------------*/
	.about-heading-common.skills-heading-comman{
		font-size: 5vw;
		padding: 1vw 0vw 1vw 2vw;
		margin: 6vw 0vw .5vw 0vw;
	}
	.about-personal-item{
		font-size: 5vw;
		margin: 1vw 0vw;
	}
	

	/*interest--------------------------------------------------------------------*/
	.skills-other-item.about-interest-item{
		margin: 1vw 0vw 1vw 0vw;
		font-size: 4vw;
	}

	/*	skills items--------------------------------------------------*/
	.skill-item-box{
		height: 15vw;
		margin-top: 1vw;
	}

	.skill-item-name{
	font-size: 3vw;
	}
}


